import { SoundManager } from "../../../../frame/scripts/Manager/SoundManager";
import { Tools } from "../../../../frame/scripts/Utils/Tools";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DanJun extends cc.Component {

    @property(sp.Skeleton)
    private meiShui: sp.Skeleton = null;
    @property(sp.Skeleton)
    private danjun: sp.Skeleton = null;
    @property(sp.Skeleton)
    private cuoWu: sp.Skeleton = null;
    @property(sp.Skeleton)
    private shengbing: sp.Skeleton = null;


    private hideNode() {
        this.meiShui.node.active = false;
        this.danjun.node.active = false;
        this.cuoWu.node.active = false;
        this.shengbing.node.active = false;
    }

    public palyDaiJi() {
        this.hideNode();
        this.danjun.node.active = true;
        Tools.playSpine(this.danjun, 'standby_loop', true)
    }

    public playMeiShui(func: Function = null) {
        this.hideNode();
        this.meiShui.node.active = true;
        SoundManager.playEffect("为什么没有水呀", false, false, false);
        Tools.playSpine(this.meiShui, 'animation', false, function () {
            this.hideNode();
            this.danjun.node.active = true;
            Tools.playSpine(this.danjun, 'standby_loop', true)
        }.bind(this))
    }

    public playCuoWu(func: Function = null) {
        this.hideNode();
        this.cuoWu.node.active = true;
        Tools.playSpine(this.cuoWu, 'animation', false, function () {
            this.hideNode();
            this.shengbing.node.active = true;
            Tools.playSpine(this.shengbing, '1', false);
        }.bind(this))
    }

    public playZhengQue(func: Function = null) {
        this.hideNode();
        this.danjun.node.active = true;
        Tools.playSpine(this.cuoWu, 'win_loop', true)
    }

}
